
import React, { Component } from 'react'
import  Swiper from 'swiper/js/swiper.js';
import "swiper/css/swiper.min.css"
import { Icon } from 'antd'

import './index.scss'

export default class Plan extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }
    componentDidMount() {
      new Swiper('.swiper-plan', {
        slideToClickedSlide: true,
        grabCursor : true,
        speed: 1000,
        spaceBetween: 40,
        initialSlide: 0,
        preventClicks: false,
        slidesPerView: 5, //
        centeredSlides: true, // active slide 
        loop: true, // 
        parallax : true,
      })
    }
    render() {
      const dataList = [
        {
          img: require('../../assets/images/traffic.png'),
          // img: '../../assets/images/traffic.png',
          title: '',
          content: ''
        }, {
          img: require('../../assets/images/tax.png'),
          // img: '../../assets/images/tax.png',
          title: '',
          content: ''
        },{
          img: require('../../assets/images/insurance.png'),
          // img: '../../assets/images/insurance.png',
          title: '',
          content: ''
        },{
          img: require('../../assets/images/money.png'),
          // img: '../../assets/images/money.png',
          title: '',
          content: ''
        },
        {
          img: require('../../assets/images/trade.png'),
          // img: '../../assets/images/trade.png',
          title: '',
          content: ''
        },
        
      ]
      const itemList = dataList.map((item, index) => {
        return (
          <div className="swiper-slide" key={index}>
            <img src={item.img} alt=""  className="plan-image"/>
            <div className="paperwork">
              <div className="paper-title"><span>{item.title}</span></div>
              <div className="paper-content">{item.content}</div>
            </div>
          </div>
        )
      })
        return (
          <div className="plan-wrapper">
            <div className="top">
              <div className="title"></div>
              <div className="more">
                <Icon type="right"/>
              </div>
            </div>
            <div className="content">
              <div className="content-wrap">
                <div className="swiper-plan">
                  <div className="swiper-wrapper">
                    {itemList}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )
    }
}
