import React, { Component } from 'react'
import { Icon, Steps } from 'antd'
import './index.scss'
import * as echarts from 'echarts/lib/echarts'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/chart/graph'
const { Step } = Steps;
const ocr = require('../../assets/images/OCR.png')
const number = require('../../assets/images/number.png')
const card = require('../../assets/images/card.png')
const three = require('../../assets/images/three.png')
const person = require('../../assets/images/person.png')
const env = require('../../assets/images/env.png')

export default class Recommend extends Component {
    constructor(props) {
        super(props)
        this.state = {
          current: 0,
          chart: null
        }
    }
    componentDidMount() {
      const chart = echarts.init(document.getElementById('logo'))
      this.setState({chart})
    }
    onChange = (current) => {
      this.setState({ current })
    }
    praseSource = (val) => {
      for (var i = 0; i < val.length; i++) {
        if (val[i].category === 6) {
          val[i] = {
            name: val[i].name,
            color: '#215D94 ',
            symbolSize: 6+4*i,
            draggable: true, 
          }
        }else {
          val[i] = {
            name: val[i].name,
            symbolSize: 65, // 
            draggable: true, // 
            category: val[i].category, // 
            itemStyle: {
              borderColor: '#fff',
              borderWidth: 4,
              shadowBlur: 10,
              shadowColor: 'rgba(98, 198, 248, 0.5)', //
            }
          }
        }
      }
      return val
    }
    praseLink = (val) => {
      for (var i = 0; i < val.length; i++) {
        if(i > 4) {
          val[i]['lineStyle'] = {
            type: 'dotted',
          }
        }else {
          val[i]['lineStyle'] = {
            type: 'solid',
          }
        }
      }
      return val
    }
    render() {
      // 01
      const sourceData = [
        {
          name: '',
          category: 2
        },{
          name: 'OCR',
          category: 0
        },{
          name: '',
          category: 1
        },{
          name: '',
          category: 3
        },{
          name: '',
          category: 4
        },{
          name: '',
          category: 5
        },{
          name: 'empty1',
          category: 6
        },{
          name: 'empty2',
          category: 6
        },{
          name: 'empty3',
          category: 6
        },{
          name: 'empty4',
          category: 6
        }
      ]
      // 
      const linkData = [
        {
          source: '',
          target: 'OCR'
        },{
          source: 'OCR',
          target: ''
        },{
          source: '',
          target: ''
        },{
          source: '',
          target: ''
        },{
          source: '',
          target: ''
        },{
          source: '',
          target: 'empty4'
        },{
          source: '',
          target: 'empty2'
        },
        {
          source: '',
          target: 'empty3'
        },{
          source: 'OCR',
          target: 'empty1'
        },
      ]
      var typeData = [
        {
        name: 'OCR',
        symbolKeepAspect: true,
        symbol: `image://${ocr}`,        
        },{
          name: '',
          symbolKeepAspect: true,
          symbol: `image://${person}`,        
        },{
          name: '',
          symbolKeepAspect: true,
          symbol: `image://${card}`,        
        },{
          name: '',
          symbolKeepAspect: true,
          symbol: `image://${env}`,        
        },{
          name: '',
          symbolKeepAspect: true,
          symbol: `image://${three}`,        
        },{
          name: '',
          symbolKeepAspect: true,
          symbol: `image://${number}`,        
        },{
          name: '',
          symbol: 'circle'
        }
      ]
      const option = {
        series: [{
          type: 'graph',
          layout: 'force',
          force: {
            repulsion: 500,
            edgeLength: 100
          },
          label: {
            normal: {
                show: true,
                color: 'rgba(0,0,0,0)'
            }
          },
          zoom: 0.9,
          emphasis: {
            focus:'adjacency',
            scale: true,
            itemStyle: {
              shadowColor: '#162d5e',
              shadowBlur: 20
            }
          },
          top: '10%',
          data: this.praseSource(sourceData),
          links: this.praseLink(linkData),
          lineStyle: {
            color: '#3385C1',
            width: 2,
            curveness: 0.2
          },
          categories: typeData 
        }]
      }
      if(this.state.chart){
        this.state.chart.off('click')
        this.state.chart.setOption(option)
        this.state.chart.on('click', (params) => {
          const category = params.data.category
          if(category < 6) {
            this.setState({
              current: category
            })
          }
        })
      }
      const { current } = this.state
      const recommentContent = [
        {
          title: 'OCR',
          description: ''
        },
        {
          title: '',
          description: 'OCR'
        },
        {
          title: '',
          description: ''
        },
        {
          title: '',
          description: 'pm2.5no2so2'
        },
        {
          title: '',
          description: 'API'
        },
        {
          title: '',
          description: ''
        },{
          title: '',
          description: ''
        }
      ]
        return (
          <div className="recommendWrapper" >
            <div className="top">
              <div className="title"></div>
              <div className="more">
                <Icon type="right"/>
              </div>
            </div>
            <div className="content-wrap">
              <div className="content">
                <div className="content-left">
                  <div id="logo" style={{width: '100%',height: '100%'}}/>
                </div>
                <div className="content-right">
                  <Steps
                    direction="vertical"
                    current={current}
                    onChange={this.onChange}
                    >
                    {
                      recommentContent.map((item, index) => {
                        return(
                          <Step
                            key={index}
                            title={item.title}
                            description={item.description}
                          />
                        )
                      })
                    }
                  </Steps>
                </div>
              </div>
            </div>
          </div>
        )
    }
}
